body {
    background-image: url(/clickyGame/static/media/bg.915c25eb.png) !important;
    margin: 0;
    font-family: sans-serif;
  }
  
  p {
    color: white;
    text-decoration: none;
  }



.header {
    
    background-image: url(/clickyGame/static/media/footer.3ecccc43.png);
    height: 150px;
    margin: 0;
    font-size: 18px;
    box-shadow: 0 0 50px 0px;
  }
  
  .green{
    color:#DE6CA9;

  }
  .red{
    color: #1D315C;

  }

  #colorChange{
   position: absolute;
   top: 0;

  }

.push{

}
.container{
    
}
.card {
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    height: 180px;
    margin: 30px;
    overflow: hidden;
    width: 180px;
    cursor: pointer;
    background-image: linear-gradient(to bottom right, #38447D, #BCFEFE, #9C99E7, #38447D,#b967ff, #A46196);
  }
  
  .card:hover {
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  
  .card-height {
    height: 100% !important;
    width: 100% !important;
  }
.footer{

    background-image: url(/clickyGame/static/media/footer.3ecccc43.png) !important;
    color: white;
    font-size: 150%;
    height: 75px;

}

